body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Logo-icon {
    color: #d92a41;
    font-weight: bold;
    display: inline-block;
    margin:0 10px;
    font-size:32px;
    -ms-user-select: none;
        user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}
.Navbar-container {
    box-shadow : 0px -2px 4px #b30c22;
    position:fixed;
    top:0;
    left:0;
    right:0;
    height: 60px;
    line-height: 60px;
    z-index:999;
}

.ContentArea-container {
    position: fixed;
    top:60px;
    bottom:0;
    left:0;
    right:0;
}

.ContentArea-container > .ContentArea-icon-tray {
    position: absolute;
    top:0;
    bottom:0;
    width:60px;
    background-color:#f0f0f0;
}

.ContentArea-container > .ContentArea-left-pane {
    position: absolute;
    left: 60px;
    width: 400px;
    top: 0;
    bottom: 0;
    background-color:#f7f5f5;
}

.ContentArea-container > .ContentArea-content-pane {
    position: absolute;
    left: 460px;
    right: 0;
    top: 0;
    bottom: 0;
}
body {
  background: -webkit-linear-gradient(315deg, rgba(253,247,247,1) 0%, rgba(255,255,255,1) 100%);
  background: linear-gradient(135deg, rgba(253,247,247,1) 0%, rgba(255,255,255,1) 100%);
}
.App {
  font-family : 'Roboto', sans-serif;
  margin-top: 60px;
}
.footer {
  box-shadow: -8px 2px 3px #000;
  position:absolute;
  bottom:0;
  left:0;
  right:0;
  height:20px;
  line-height: 20px;
  text-align: center;
  background-color: #f9f9f9;
  padding:10px;
  color:#d3d3d6 !important;
  font-family: sans-serif;
  font-size: 12px;
}
.footer a {
  color : #d3d3d6 !important;
}
